import React, { lazy } from "react";
import { homeMenu } from "../menu";

const HOME = {
    SEARCH: lazy(() => import("../pages/home/HomePage")),
    TEST: lazy(() => import("../pages/test/Test")),
};

const present = [
    {
        path: homeMenu.search.path,
        element: <HOME.SEARCH />,
        exact: true,
    },
    {
        path: homeMenu.test.path,
        element: <HOME.TEST />,
        exact: true,
    },
];

const contents = [...present];

export default contents;
